@import "./variable";

body, html {
  // min-width: 980px;
  line-height: 1;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, 'Microsoft Yahei', sans-serif, 'Droid Sans Fallback';
  // user-select: none;
  -webkit-tap-highlight-color: transparent;
  background: $darkWhite;
  color: $lightBlack;
  padding-bottom: 20px;
}

[class^="el-icon-fa"], [class*=" el-icon-fa"] {
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome!important;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
};


